import React from 'react';
import { Panel, Button } from '@vkontakte/vkui/';
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline';
import Icon28ErrorOutline from '@vkontakte/icons/dist/28/error_outline';
import Icon28GraphOutline from '@vkontakte/icons/dist/28/graph_outline';
import './Style.css';

const Home = (props) => {

	const goRating = () => {
		props.goToPage('Rating')
	}
	const startGame = () => {

		if (props.position === undefined || props.position === '') {

			props.goToPage('Warning')

		} else {

			props.goToPage('Play')

		}
	}

	const changeLevel = () => {
		props.changeLevel()
	}


	return (
		<Panel id={props.id} separator={false}>
			<div className="stage" style={{ overflow: 'hidden'}}>
				{props.animation === true ? (
				<>
					
						<ul className='circles'>
							<li></li>
							<li></li>
							<li></li>
							<li></li>
							<li></li>
							<li></li>
							<li></li>
							<li></li>
							<li></li>
							<li></li>
							<li></li>
							<li></li>
							<li></li>
							<li></li>
							<li></li>

						</ul>
				</>
			) : (
					<></>
				)}
			
				<div className="title">Towner</div>
				<Button style={{ cursor: 'pointer' }} className="startButton" mode="outline" size="xl" onClick={() => startGame()}><div className="titleStartBut">!</div></Button>
				<Button style={{ cursor: 'pointer' }} className="levelButton" onClick={() => changeLevel()} size='xl' mode='outline'><div className="titleNextBut">{props.chooseLevel}</div></Button>

				<div className="blockButton">
					<Button style={{ cursor: 'pointer' }} 
						className="settingsButton"
						before={<Icon28SettingsOutline width={41} height={41} style={{ color: "white" }} />}
						mode="outline"
						size=""
						onClick={() => props.goToPage('Settings')}
					/>
					<Button style={{ cursor: 'pointer' }} 
						className="ratingButton"
						before={<Icon28GraphOutline width={41} height={41} style={{ color: "white" }} />}
						mode="outline"
						size=""
						onClick={() => goRating()}
					>
						<p style={{
							color: "white",
							fontSize: 30,
							fontFamily: 'Neucha',
						}}></p>
					</Button>
					<Button style={{ cursor: 'pointer'}} 
						className="aboutButton"
						before={<Icon28ErrorOutline width={41} height={41} style={{ color: "white" }} />}
						mode="outline"
						data-to="persik"
						size=""
						onClick={() => props.goToPage('Info')}
					>
						<p style={{
							color: "white",
							fontSize: 30,
							fontFamily: 'Neucha',
						}}></p>
					</Button>
				</div>
			</div>
		</Panel>
	)
};

export default Home;
