import React, { useState } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { Panel, PanelHeader, List, Div, Cell, PanelHeaderBack, Button, Group, Header, Separator, Input } from '@vkontakte/vkui/';
import Icon28PlaceOutline from '@vkontakte/icons/dist/28/place_outline';
import './Style.css';

require('dotenv').config();

const Settings = (props) => {
    const [pos, setPos] = useState()

    const [errorSearch, setErrorSearch] = useState(false)
    const [errorSearchValue, setErrorSearchValue] = useState()

    const [lengthSearch, setStateLengthSearch] = useState(0)

    const [descriptionAdress0, setStateDescriptionAdress0] = useState('')
    const [nameAdress0, setStateNameAdress0] = useState()
    const [position0, setStatePosition0] = useState()

    const [descriptionAdress1, setStateDescriptionAdress1] = useState('')
    const [nameAdress1, setStateNameAdress1] = useState()
    const [position1, setStatePosition1] = useState()


    const [descriptionAdress2, setStateDescriptionAdress2] = useState()
    const [nameAdress2, setStateNameAdress2] = useState()
    const [position2, setStatePosition2] = useState()

    const [description0, setStateDescription0] = useState('    .')
    const [description1, setStateDescription1] = useState(',       .')

    const [chooseGeo0, setStateChooseGeo0] = useState()
    const [chooseGeo1, setStateChooseGeo1] = useState()


    const [valueInput, setValueInput] = useState()




    const searchChange = () => {
        let titleRegExp = /[!@#$%^&*()_+?><:"}{|~`,/+\`}1234567890;'".d]/;
        if (titleRegExp.test(document.getElementById('search').value)) {
            setErrorSearch(true)
            setErrorSearchValue('  .')
        } else {
            let titleRegExp1 = new RegExp("[--A-Za-z\s]{15,}");
            if (titleRegExp1.test(document.getElementById('search').value)) {
                setErrorSearch(true)
                setErrorSearchValue('  .')
            } else {
                setValueInput(document.getElementById('search').value)
                setStateLengthSearch(document.getElementById('search').value.length)
                const key = `c16841d0-5344-4254-a4fd-32e30927fa2a`;
                const api = `https://geocode-maps.yandex.ru/1.x/?format=json&geocode=${document.getElementById('search').value}&apikey=${key}`;
                if (document.getElementById('search').value.length > 0) {
                    fetch(api)
                        .then(res => res.json())
                        .then(data => {
                            if (isNaN(document.getElementById('search').value)) {
                                setErrorSearch(false)
                                
                                setStateNameAdress0((data.response.GeoObjectCollection.featureMember[0]).GeoObject.name)
                                setStatePosition0((data.response.GeoObjectCollection.featureMember[0]).GeoObject.Point.pos.split(' ').reverse())
                                setStateDescriptionAdress0((data.response.GeoObjectCollection.featureMember[0]).GeoObject.description)
                                setStateNameAdress1((data.response.GeoObjectCollection.featureMember[1]).GeoObject.name)
                                setStatePosition1((data.response.GeoObjectCollection.featureMember[1]).GeoObject.Point.pos.split(' ', 2).reverse())
                                setStateDescriptionAdress1((data.response.GeoObjectCollection.featureMember[1]).GeoObject.description)
                                setStateNameAdress2((data.response.GeoObjectCollection.featureMember[2]).GeoObject.name)
                                setStateDescriptionAdress2((data.response.GeoObjectCollection.featureMember[2]).GeoObject.description)
                                setStatePosition2((data.response.GeoObjectCollection.featureMember[2]).GeoObject.Point.pos.split(' ', 2).reverse())
                            }
                            else {
                                if (document.getElementById('search').value.length > 0) {
                                    setErrorSearch(true)
                                    setErrorSearchValue('  .')
                                }


                            }
                            if (document.getElementById('search').value.length === 0) {
                                setErrorSearch(false)
                            }


                        })

                        .catch(e => {
                            if (document.getElementById('search').value.length > 20) {
                                setErrorSearch(true)
                                setErrorSearchValue('  .')
                            }
                            if (document.getElementById('search').value.length === 0) {
                                setErrorSearch(false)
                            }
                        })
                }
            }
            if (document.getElementById('search').value.length === 0) {
                setErrorSearch(false)
            }
        }

        if (document.getElementById('search').value.length === 0) {
            setErrorSearch(false)
        }
    }
    const getPosition0 = () => {

        if (descriptionAdress0 === undefined || descriptionAdress0 === '') {
            setStateChooseGeo0()
            setStateDescription0('   ,  GPS.')
            document.getElementById('search').value = ''
            setStateLengthSearch(0)
        }
        else {
            setStateDescription0('  /     .')
            setStateDescription1(',       .')
            setStateChooseGeo0(`${descriptionAdress0}, ${nameAdress0}.`)
            setStateChooseGeo1()
            document.getElementById('search').value = ''
            
            
            setStateLengthSearch(0)
            setStateNameAdress0()
            setStateDescriptionAdress0()
            setStateNameAdress1()
            setStateDescriptionAdress1()
            setStateNameAdress2()
            setStateDescriptionAdress2()
            props.savePositionGlobal(`${position0}`, `${descriptionAdress0}, ${nameAdress0}`)
            props.goToPage('Home')
            bridge.send("VKWebAppStorageSet", { "key": "typeGeo", "value": 'maps' });
        }

    }
    const getPosition1 = () => {

        if (descriptionAdress1 === undefined || descriptionAdress1 === '') {
            setStateChooseGeo0()
            setStateDescription0('   ,  GPS.')
            document.getElementById('search').value = ''
            setStateLengthSearch(0)
        }
        else {
            setStateDescription0('  /     .')
            setStateDescription1(',       .')
            setStateChooseGeo0(`${descriptionAdress0}, ${nameAdress0}.`)
            setStateChooseGeo1()
            document.getElementById('search').value = ''
            
            setStateLengthSearch(0)
            setStateNameAdress0()
            setStateDescriptionAdress0()
            setStateNameAdress1()
            setStateDescriptionAdress1()
            setStateNameAdress2()
            setStateDescriptionAdress2()
            props.savePositionGlobal(`${position1}`, `${descriptionAdress1}, ${nameAdress1}`)
            props.goToPage('Home')
            bridge.send("VKWebAppStorageSet", { "key": "typeGeo", "value": 'maps' });
        }

    }
    const getPosition2 = () => {

        if (descriptionAdress2 === undefined || descriptionAdress2 === '') {
            setStateDescription0('   ,  GPS.')
            setStateChooseGeo0()
            document.getElementById('search').value = ''
            setStateLengthSearch(0)
        }
        else {
            setStateDescription0('  /     .')
            setStateDescription1(',       .')
            setStateChooseGeo0(`${descriptionAdress0}, ${nameAdress0}.`)
            setStateChooseGeo1()
            document.getElementById('search').value = ''
          
            setStateLengthSearch(0)
            setStateNameAdress0()
            setStateDescriptionAdress0()
            setStateNameAdress1()
            setStateDescriptionAdress1()
            setStateNameAdress2()
            setStateDescriptionAdress2()
            props.savePositionGlobal(`${position2}`, `${descriptionAdress2}, ${nameAdress2}`)
            props.goToPage('Home')
            bridge.send("VKWebAppStorageSet", { "key": "typeGeo", "value": 'maps' });
        }
    }

    const getPositionWithGps = () => {


        bridge.send("VKWebAppGetGeodata")
            .then(data => {

                if (data.lat === undefined) {
                    setStateDescription1('  .       .')
                } else {
                    setPos(data.lat)
                    const key = `c16841d0-5344-4254-a4fd-32e30927fa2a`;
                    const api = `https://geocode-maps.yandex.ru/1.x/?format=json&geocode=${data.long},${data.lat}&apikey=${key}`;

                    fetch(api)
                        .then(res => res.json())
                        .then(data => {
                            props.goToPage('Home')
                            setStateDescription0('    .')
                            setStateDescription1('  /     .')
                            setStateChooseGeo0()
                            console.log((data.response.GeoObjectCollection.featureMember[0]).GeoObject.Point.pos.split(' ', 2)[0]);
                            setStateChooseGeo1((data.response.GeoObjectCollection.featureMember[0]).GeoObject.description)
                            props.savePositionGlobal(`${(data.response.GeoObjectCollection.featureMember[0]).GeoObject.Point.pos.split(' ', 2)[1]},${(data.response.GeoObjectCollection.featureMember[0]).GeoObject.Point.pos.split(' ', 2)[0]}`, (data.response.GeoObjectCollection.featureMember[0]).GeoObject.description)
                            bridge.send("VKWebAppStorageSet", { "key": "typeGeo", "value": 'gps' });
                        })

                        .catch(e => {
                            if (isNaN(pos)) {
                                setStateDescription1('  .        /    .')
                            }
                            else {

                            }

                        })






                }

            })
            .catch(error => {
                setStateDescription1('  .        /    .')
            })

    }


    return (

        <Panel id={props.id} separator={false}>
            <PanelHeader transparent left={<PanelHeaderBack style={{ cursor: 'pointer' }} onClick={() => window.history.back()} />}>
                
        </PanelHeader>
            <div className='stage' style={{ overflow: 'hidden' }}>

                <div style={{ position: 'relative', marginTop: '21vh' }}>
                    <Separator />
                    <Group>
                        <h1 style={{textAlign: 'center'}}>    </h1>
                        <Header> </Header>
                        <Div>
                            <Input style={{ cursor: 'pointer' }} defaultValue={valueInput} id='search' onChange={() => searchChange()} placeholder=''></Input>

                        </Div>
                        {errorSearch === false ? (
                            <>
                                {lengthSearch > 0 ? (
                                    <List >
                                        <Cell multiline={true}
                                            description={descriptionAdress0}
                                            onClick={() => getPosition0()}>
                                            {nameAdress0}

                                        </Cell>
                                        <Cell multiline={true}
                                            description={descriptionAdress1}
                                            onClick={() => getPosition1()}>
                                            {nameAdress1}
                                        </Cell>
                                        <Cell multiline={true}
                                            description={descriptionAdress2}
                                            onClick={() => getPosition2()}>
                                            {nameAdress2}
                                        </Cell>
                                    </List>
                                ) : (
                                        <Cell multiline={true}
                                            description={description0}>
                                            {chooseGeo0}
                                        </Cell>
                                    )}
                            </>
                        ) : (
                                <> <Cell multiline={true}>

                                    {errorSearchValue}
                                </Cell>

                                </>
                            )}
                    </Group>
                    <Group header={
                        <Header>  GPS</Header>}>
                        <Div>
                            <Button style={{ cursor: 'pointer' }} mode='outline' before={<Icon28PlaceOutline style={{ color: "white" }} />} onClick={() => getPositionWithGps()} size='xl'> </Button></Div>
                        <Cell description={description1} multiline={true}>
                            {chooseGeo1}
                        </Cell>
                    </Group>
                </div>
            </div>
        </Panel>
    )
};

export default Settings;





