import React, { useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { Panel } from '@vkontakte/vkui/';
import './Style.css';
import { firebase } from './Firebase.config.js';


const Loader = props => {

  
    useEffect(() => {
        


        
       let timer = setInterval(() => {
            setTimeout(() => {

                bridge.send("VKWebAppStorageGet", { "keys": ["hasSeenOnboarding"] })
                    .then(data => {
                        if (data.keys[0].value === 'true') {
                            props.goToHome()
                            clearInterval(timer)

                        } else {
                            props.goToIntro()
                            clearInterval(timer)
                        }
                    })
                    .catch(e => {
                    })
            }, 500);
        }, 1000);
        
        
        


    }, []);

    return (

        <Panel id={props.id} separator={false}>
            <div className="stage">
              
                <div className="blockLoaderForPanel">
                    <div className="earthLoaderForPanel"></div>
                </div>


            </div>
        </Panel>
    )
};

export default Loader;