import React, { useEffect, useState } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { Panel, PanelHeader, Separator, PanelHeaderBack, RichCell, Header, Group, Avatar, ScreenSpinner, Div, Button } from '@vkontakte/vkui/';
import { firebase } from './Firebase.config.js';
import Icon28ShareOutline from '@vkontakte/icons/dist/28/share_outline';
import './Style.css';
require('dotenv').config();

const Rating = (props) => {
	const [animation, setAnimation] = useState(props.animation)

	const [error, setError] = useState(false)

	const [userName, setUserName] = useState()
	const [photo, setPhoto] = useState()
	const [score, setScore] = useState()

	const [userName0, setUserName0] = useState()
	const [photo0, setPhoto0] = useState()
	const [score0, setScore0] = useState()
	const [link0, setLink0] = useState()


	const [userName1, setUserName1] = useState()
	const [photo1, setPhoto1] = useState()
	const [score1, setScore1] = useState()
	const [link1, setLink1] = useState()

	const [userName2, setUserName2] = useState()
	const [photo2, setPhoto2] = useState()
	const [score2, setScore2] = useState()
	const [link2, setLink2] = useState()

	const [userName3, setUserName3] = useState()
	const [photo3, setPhoto3] = useState()
	const [score3, setScore3] = useState()
	const [link3, setLink3] = useState()

	const [userName4, setUserName4] = useState()
	const [photo4, setPhoto4] = useState()
	const [score4, setScore4] = useState()
	const [link4, setLink4] = useState()

	const [userName5, setUserName5] = useState()
	const [photo5, setPhoto5] = useState()
	const [score5, setScore5] = useState()
	const [link5, setLink5] = useState()

	const [userName6, setUserName6] = useState()
	const [photo6, setPhoto6] = useState()
	const [score6, setScore6] = useState()
	const [link6, setLink6] = useState()

	const [allPlayers, setallPlayers] = useState()


	useEffect(() => {




		function checkOnlineState() {
			if (navigator.onLine) {

			} else {
				setError(true)
				console.log(' ');
			}
		}
		checkOnlineState();
		window.addEventListener('online', checkOnlineState);

		firebase.firestore().collection(toString(process.env.vn845gh845gi4ufe782dermj394)).get()
			.then((snapshot) => {

				const allUsersRating = snapshot.docs.map((obj) => (
					{ ...obj.data() }
				))
				allUsersRating.sort(function (a, b) {
					return b['score'] - a['score'];
				});

				setallPlayers(allUsersRating.length)
				setUserName0(allUsersRating[0].first_name.replace('&#39;', "'") + ' ' + allUsersRating[0].last_name.replace('&#39;', "'"))
				setPhoto0(allUsersRating[0].photo)
				setScore0(allUsersRating[0].score)
				setLink0(allUsersRating[0].link)

				setUserName1(allUsersRating[1].first_name.replace('&#39;', "'") + ' ' + allUsersRating[1].last_name.replace('&#39;', "'"))
				setPhoto1(allUsersRating[1].photo)
				setScore1(allUsersRating[1].score)
				setLink1(allUsersRating[1].link)

				setUserName2(allUsersRating[2].first_name.replace('&#39;', "'") + ' ' + allUsersRating[2].last_name.replace('&#39;', "'"))
				setPhoto2(allUsersRating[2].photo)
				setScore2(allUsersRating[2].score)
				setLink2(allUsersRating[2].link)

				setUserName3(allUsersRating[3].first_name.replace('&#39;', "'") + ' ' + allUsersRating[3].last_name.replace('&#39;', "'"))
				setPhoto3(allUsersRating[3].photo)
				setScore3(allUsersRating[3].score)
				setLink3(allUsersRating[3].link)

				setUserName4(allUsersRating[4].first_name.replace('&#39;', "'") + ' ' + allUsersRating[4].last_name.replace('&#39;', "'"))
				setPhoto4(allUsersRating[4].photo)
				setScore4(allUsersRating[4].score)
				setLink4(allUsersRating[4].link)



			}).catch(err => {
				console.log('    ');
			})







		bridge.send("VKWebAppGetUserInfo")
			.then(data => {

				setUserName(data.first_name.replace('&#39;', "'") + ' ' + data.last_name.replace('&#39;', "'"))
				setPhoto(data.photo_200)

				firebase.firestore().collection(toString(process.env.vn845gh845gi4ufe782dermj394)).doc(data.id.toString()).get()
					.then((doc) => {
						setScore(doc.data().score)
					}).catch(error => {
						console.log('  ');
						firebase.firestore().collection(toString(process.env.vn845gh845gi4ufe782dermj394)).doc(data.id.toString()).set({
							score: 0,
							first_name: data.first_name,
							last_name: data.last_name,
							photo: data.photo_200,
						})
						setScore(0)
					})
			})
			.catch(error => {
				setError(true)
			})

	}, [firebase])

	return (

		<Panel id={props.id} separator={false} >

			{error === false ? (
				<>
					<PanelHeader transparent left={<PanelHeaderBack style={{ cursor: 'pointer' }} onClick={() => window.history.go(-1)} />}>
						{props.alertText}
					</PanelHeader>
					<div className="stage" style={{ overflow: 'hidden' }}>
						{animation === true ? (
							<>

								<ul className='circles'>
									<li></li>
									<li></li>
									<li></li>
									<li></li>
									<li></li>
									<li></li>
									<li></li>
									<li></li>
									<li></li>
									<li></li>
									<li></li>
									<li></li>
									<li></li>
									<li></li>
									<li></li>

								</ul>
							</>
						) : (
								<></>
							)}
						<div style={{ position: 'relative', marginTop: '21vh' }}>
							<Separator />
							{score >= 0 ? (

								<>
									{props.alert === false ? (
										<Group header={
											<Header>     {allPlayers}</Header>}>
											{score0 > 0 ? (
												<RichCell style={{ cursor: 'pointer' }} target='_blank' href={link0} before={<Avatar size={48} src={photo0} />} after={`${score0}` + ' '}>{userName0}</RichCell>

											) : (
													<></>
												)}
											{score1 > 0 ? (
												<RichCell style={{ cursor: 'pointer' }} target='_blank' href={link1} before={<Avatar size={48} src={photo1} />} after={`${score1}` + ' '}>{userName1}</RichCell>

											) : (
													<></>
												)}
											{score2 > 0 ? (
												<RichCell style={{ cursor: 'pointer' }} target='_blank' href={link2} before={<Avatar size={48} src={photo2} />} after={`${score2}` + ' '}>{userName2}</RichCell>

											) : (
													<></>
												)}
											{score3 > 0 ? (
												<RichCell style={{ cursor: 'pointer' }} target='_blank' href={link3} before={<Avatar size={48} src={photo3} />} after={`${score3}` + ' '}>{userName3}</RichCell>

											) : (
													<></>
												)}
											{score4 > 0 ? (
												<RichCell style={{ cursor: 'pointer' }} target='_blank' href={link4} before={<Avatar size={48} src={photo4} />} after={`${score4}` + ' '}>{userName4}</RichCell>

											) : (
													<></>
												)}
											<RichCell style={{ pointerEvents: 'none' }} after={`${score}` + ' '}> </RichCell>
											<Div>
												<Button style={{ cursor: 'pointer' }} mode='outline' size='xl' before={<Icon28ShareOutline style={{ color: "white" }} />} onClick={() => props.shareResult(score)}></Button>
											</Div>
											{props.snackbar}
										</Group>

									) : (
											<></>
										)}


								</>

							) : (
									<>
										<div className="stage">
											<ul className='circles'>
												<li></li>
												<li></li>
												<li></li>
												<li></li>
												<li></li>
												<li></li>
												<li></li>
												<li></li>
												<li></li>
												<li></li>
												<li></li>
												<li></li>
												<li></li>
												<li></li>
												<li></li>
											</ul>


											<div className="blockLoaderForPanel">
												<div className="earthLoaderForPanel"></div>
											</div>


										</div>
									</>
								)}



						</div></div></>
			) : (
					<>
						<PanelHeader transparent left={<PanelHeaderBack style={{ cursor: 'pointer' }} onClick={() => window.history.go(-1)} />}>

						</PanelHeader>
						<div className="stage">
							<ul className='circles'>
								<li></li>
								<li></li>
								<li></li>
								<li></li>
								<li></li>
								<li></li>
								<li></li>
								<li></li>
								<li></li>
								<li></li>
								<li></li>
								<li></li>
								<li></li>
								<li></li>
								<li></li>
							</ul>


							<div className="blockLoaderForPanel">
								<div className="titleLoader">   .    .</div>
								<div className="earthLoaderForPanel"></div>
							</div>


						</div>
					</>
				)}



		</Panel>
	)
};

export default Rating;





