import React, {useState} from 'react';
import bridge from '@vkontakte/vk-bridge';
import { Div, Gallery, Panel, Button } from '@vkontakte/vkui/';
import Arrow from '../img/arrow.svg';
import { firebase } from './Firebase.config.js';
import './Style.css';

const Intro = props => {
const [slide, setSlide] = useState(0)
    const firstOpenApp = () => {

        bridge.send("VKWebAppGetUserInfo")
            .then(data => {

                const id = data.id.toString()

                firebase.firestore().collection('globalRating').doc(id).set({
                    score: 0,
                })

                    .catch(err => {
                        console.log(err)
                    })
            })
        bridge.send("VKWebAppStorageSet", { "key": "hasSeenOnboarding", "value": 'true' });
        bridge.send("VKWebAppStorageSet", { "key": "geo", "value": '' });
        bridge.send("VKWebAppStorageSet", { "key": "pos", "value": '' });

        props.goToHome()
    }

    return (

        <Panel id={props.id} separator={false}>
            <div className="stage" style={{ overflow: 'hidden' }}>
                <ul class="circlesIntro">
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                    <li></li>
                </ul>
                <Gallery onChange={slideIndex => setSlide(slideIndex)} slideIndex={slide} className='gallery' slideWidth="100%" bullets="false">


                    <div className='galleryBlock'>
                        <div className='textIntro' style={{ fontSize: '5vh' }}>       Towner!</div>
                        <img className="scale" src={Arrow} />
                        <Div><Button mode='tertiary' onClick={() => setSlide(1)} size="xl" className="nextButtonIntro"><h1>!</h1></Button></Div>
                    </div>
                    <div className='galleryBlock'>
                        <div className='textIntro' style={{ fontSize: '3.5vh' }}>  ,           !</div>
                        <img className="scale" src={Arrow} />
                        <Div><Button mode="tertiary" onClick={() => setSlide(2)} size="xl" className="nextButtonIntro"><h1>!</h1></Button></Div>
                    </div>
                    <div className='galleryBlock'>
                        <div className='textIntro' style={{ fontSize: '3.5vh' }}> :     //      !</div>
                        <img className="scale" src={Arrow} />
                        <Div><Button mode="tertiary" onClick={() => setSlide(3)} size="xl" className="nextButtonIntro"><h1>!</h1></Button></Div>
                    </div>
                    <div className='galleryBlock'>
                        <div className='textIntro' style={{ fontSize: '4vh' }}>          !</div>
                        <Button style={{ cursor: 'pointer' }} className="startButIntro" mode="outline" size="xl" onClick={() => firstOpenApp()}><div className="titleStartBut" style={{ fontSize: '6vh' }}>!</div></Button>
                    </div>
                </Gallery>
            </div>
        </Panel>
    )
};

export default Intro;