							setStateCount(100);
							setStaterightCityDictance(getDistanceFromLatLonInKmRight(props.position.split(',')[0], props.position.split(',')[1], latRight0, longRight0).toFixed(0) + " ");
							setStateleftCityDictance(getDistanceFromLatLonInKmLeft(props.position.split(',')[0], props.position.split(',')[1], latLeft0, longLeft0).toFixed(0) + " ");



